import Highway from "@dogstudio/highway";
import gsap from "gsap";

import gameRenderer from "./games";
import musicRenderer from "./music";
import artRenderer from "./art";
import meRenderer from "./me";
import indexRenderer from ".";
import gameGalleryRenderer from "../games/GameGallery";

class Slide extends Highway.Transition {
  in({from, to, done}) {
      window.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth'
        });
      gsap.fromTo(from, {opacity: '100%', left: '0%'}, {opacity: '0%', left: '-100%', duration: 1, onComplete:function() {
      }});
      gsap.fromTo(to, {opacity: '0%', left: '-100%'}, {opacity: '100%', left: '0%', duration: 1, onComplete: function() { 
          from.remove();
          done();
      }});
  }
  out({from, done}) {
      done();
  }
}

const H = new Highway.Core({
    transitions: {
      default: Slide
    },
    renderers: {
      maingames: gameRenderer,
      mainart: artRenderer,
      mainmusic: musicRenderer,
      mainme: meRenderer,
      main: indexRenderer,
      maingamegallery: gameGalleryRenderer
    }
});

H.on('NAVIGATE_END', ({ location }) => {
  var articleItems = document.querySelectorAll("article");
  for (var i = 0; i < articleItems.length - 1; ++i) {
      console.log("Get Obliterated")
      articleItems[i].remove();
  }
  // Check Anchor
  if (location.anchor) {
    // Get element
    const el = document.querySelector(location.anchor);

    if (el) {
      // Scroll to element
      window.scrollTo(el.offsetLeft, el.offsetTop);
    }
  }
});

H.on('NAVIGATE_IN', ({ to, location }) => {
});

var light_mode = false;
const themeButton = document.getElementById('color-mode');
const moon = document.getElementById('moon');
const sun = document.getElementById('sun');
let body = document.body;

themeButton.onclick = () => {
if (light_mode) {
  light_mode = false;
  body.classList.replace('light', 'dark');
  moon.classList.replace('other-mode', 'current-mode');
  sun.classList.replace('current-mode', 'other-mode');
} else {
  light_mode = true;
  body.classList.replace('dark', 'light');
  moon.classList.replace('current-mode', 'other-mode');
  sun.classList.replace('other-mode', 'current-mode');
}
}